'use client'

import { useState } from 'react'
import FullCalendar from '@fullcalendar/react'
import dayGridPlugin from '@fullcalendar/daygrid'
import interactionPlugin from '@fullcalendar/interaction'
import { EventInput } from '@fullcalendar/core'
import nbLocale from '@fullcalendar/core/locales/nb'

interface Event {
  id: string
  title: string
  date: string
  startTime: string
  endTime: string
  type: 'møte' | 'inspeksjon' | 'oppgave'
  customer?: string
}

export default function Planlegging() {
  const [events, setEvents] = useState<Event[]>([
    {
      id: '1',
      title: 'Møte med Acme AS',
      date: '2024-03-20',
      startTime: '10:00',
      endTime: '11:00',
      type: 'møte',
      customer: 'Acme AS'
    },
    // ... flere hendelser
  ])

  const [showAddModal, setShowAddModal] = useState(false)
  const [selectedDate, setSelectedDate] = useState<string>(new Date().toISOString().split('T')[0])

  const handleDateSelect = (selectInfo: any) => {
    const title = prompt('Skriv inn aktivitetsnavn:')
    if (title) {
      const calendarApi = selectInfo.view.calendar
      calendarApi.unselect() // fjern valg etter at hendelsen er lagt til

      setEvents([...events, {
        id: createEventId(),
        title,
        date: selectInfo.startStr.split('T')[0],
        startTime: selectInfo.startStr.split('T')[1],
        endTime: selectInfo.endStr.split('T')[1],
        type: 'oppgave',
      }])
    }
  }

  const handleEventDrop = (dropInfo: any) => {
    const updatedEvents = events.map(event => {
      if (event.id === dropInfo.event.id) {
        return {
          ...event,
          date: dropInfo.event.startStr.split('T')[0],
          startTime: dropInfo.event.startStr.split('T')[1],
          endTime: dropInfo.event.endStr.split('T')[1],
        }
      }
      return event
    })
    setEvents(updatedEvents)
  }

  const handleEventChange = (changeInfo: any) => {
    // Håndter endringer i hendelser
    const updatedEvents = events.map(event => {
      if (event.id === changeInfo.event.id) {
        return {
          ...event,
          title: changeInfo.event.title,
          date: changeInfo.event.startStr.split('T')[0],
          startTime: changeInfo.event.startStr.split('T')[1],
          endTime: changeInfo.event.endStr.split('T')[1],
        }
      }
      return event
    })
    setEvents(updatedEvents)
  }

  const createEventId = () => {
    return String(Date.now())
  }

  return (
    <div className="min-h-screen bg-gray-900 p-8 ml-64">
      <div className="max-w-7xl mx-auto">
        {/* Header */}
        <div className="flex justify-between items-center mb-12">
          <div>
            <h1 className="text-4xl font-bold text-white">Planlegging</h1>
            <p className="text-gray-400 mt-2">Administrer kalender og hendelser</p>
          </div>
          <button
            onClick={() => setShowAddModal(true)}
            className="bg-blue-600 hover:bg-blue-700 text-white px-6 py-3 rounded-full flex items-center gap-2 shadow-sm transition-all duration-200"
          >
            <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4v16m8-8H4" />
            </svg>
            <span>Ny hendelse</span>
          </button>
        </div>

        {/* Kalender */}
        <div className="bg-gray-800 rounded-2xl p-6 border border-gray-700">
          <div className="flex justify-between items-center mb-6">
            <div className="flex items-center gap-4">
              <button className="p-2 hover:bg-gray-700 rounded-lg">
                <svg className="w-5 h-5 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
                </svg>
              </button>
              <h2 className="text-xl font-semibold text-white">Mars 2024</h2>
              <button className="p-2 hover:bg-gray-700 rounded-lg">
                <svg className="w-5 h-5 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
                </svg>
              </button>
            </div>
            <div className="flex gap-2">
              <button className="px-4 py-2 text-gray-400 hover:text-white hover:bg-gray-700 rounded-lg">
                Dag
              </button>
              <button className="px-4 py-2 text-gray-400 hover:text-white hover:bg-gray-700 rounded-lg">
                Uke
              </button>
              <button className="px-4 py-2 bg-blue-600 text-white rounded-lg">
                Måned
              </button>
            </div>
          </div>

          {/* Kalenderoversikt */}
          <div className="grid grid-cols-7 gap-1">
            {['Man', 'Tir', 'Ons', 'Tors', 'Fre', 'Lør', 'Søn'].map(day => (
              <div key={day} className="text-center text-sm font-medium text-gray-400 py-2">
                {day}
              </div>
            ))}
            {Array.from({ length: 31 }, (_, i) => (
              <div
                key={i}
                className={`aspect-square p-2 border border-gray-700 hover:bg-gray-700/50 cursor-pointer ${
                  i === 0 ? 'col-start-5' : ''
                }`}
              >
                <div className="text-sm text-gray-300">{i + 1}</div>
                <div className="mt-1 space-y-1">
                  {events
                    .filter(event => new Date(event.date).getDate() === i + 1)
                    .map(event => (
                      <div
                        key={event.id}
                        className={`text-xs p-1 rounded ${
                          event.type === 'møte'
                            ? 'bg-blue-900/50 text-blue-400'
                            : event.type === 'inspeksjon'
                            ? 'bg-green-900/50 text-green-400'
                            : 'bg-purple-900/50 text-purple-400'
                        }`}
                      >
                        {event.title}
                      </div>
                    ))}
                </div>
              </div>
            ))}
          </div>
        </div>

        {/* Dagens hendelser */}
        <div className="mt-8 bg-gray-800 rounded-2xl p-6 border border-gray-700">
          <h2 className="text-xl font-semibold text-white mb-4">Dagens hendelser</h2>
          <div className="space-y-4">
            {events
              .filter(event => new Date(event.date).toDateString() === new Date().toDateString())
              .map(event => (
                <div key={event.id} className="flex items-center justify-between py-3 border-b border-gray-700 last:border-b-0">
                  <div className="flex items-center gap-4">
                    <div className={`w-2 h-2 rounded-full ${
                      event.type === 'møte'
                        ? 'bg-blue-500'
                        : event.type === 'inspeksjon'
                        ? 'bg-green-500'
                        : 'bg-purple-500'
                    }`} />
                    <div>
                      <p className="text-white">{event.title}</p>
                      <p className="text-sm text-gray-400">
                        {event.startTime} - {event.endTime}
                      </p>
                    </div>
                  </div>
                  <div className="flex gap-2">
                    <button className="text-blue-400 hover:text-blue-300">
                      <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15.232 5.232l3.536 3.536m-2.036-5.036a2.5 2.5 0 113.536 3.536L6.5 21.036H3v-3.572L16.732 3.732z" />
                      </svg>
                    </button>
                    <button className="text-red-400 hover:text-red-300">
                      <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16" />
                      </svg>
                    </button>
                  </div>
                </div>
              ))}
          </div>
        </div>
      </div>

      {/* Modal for å legge til ny hendelse */}
      {showAddModal && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center">
          <div className="bg-gray-800 rounded-2xl p-8 max-w-md w-full mx-4">
            <h2 className="text-2xl font-semibold text-white mb-6">Ny hendelse</h2>
            <form className="space-y-4">
              <div>
                <label className="block text-sm font-medium text-gray-400 mb-2">Tittel</label>
                <input
                  type="text"
                  className="w-full rounded-xl border-gray-700 focus:ring-blue-500 focus:border-blue-500 bg-gray-700 text-gray-300"
                />
              </div>
              <div className="grid grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm font-medium text-gray-400 mb-2">Dato</label>
                  <input
                    type="date"
                    className="w-full rounded-xl border-gray-700 focus:ring-blue-500 focus:border-blue-500 bg-gray-700 text-gray-300"
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-400 mb-2">Starttid</label>
                  <input
                    type="time"
                    className="w-full rounded-xl border-gray-700 focus:ring-blue-500 focus:border-blue-500 bg-gray-700 text-gray-300"
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-400 mb-2">Sluttid</label>
                  <input
                    type="time"
                    className="w-full rounded-xl border-gray-700 focus:ring-blue-500 focus:border-blue-500 bg-gray-700 text-gray-300"
                  />
                </div>
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-400 mb-2">Type</label>
                <select className="w-full rounded-xl border-gray-700 focus:ring-blue-500 focus:border-blue-500 bg-gray-700 text-gray-300">
                  <option value="møte">Møte</option>
                  <option value="inspeksjon">Inspeksjon</option>
                  <option value="oppgave">Oppgave</option>
                </select>
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-400 mb-2">Kunde</label>
                <input
                  type="text"
                  className="w-full rounded-xl border-gray-700 focus:ring-blue-500 focus:border-blue-500 bg-gray-700 text-gray-300"
                />
              </div>
              <button type="submit" className="w-full bg-blue-600 hover:bg-blue-700 text-white px-6 py-3 rounded-full">
                Lagre
              </button>
            </form>
          </div>
        </div>
      )}
    </div>
  )
} 